<template>
	<div class="goods-detail">
		<div class="swiper-container">
	        <div class="swiper-wrapper">
				<div class="swiper-slide"><img src="../assets/clothes-bg.png"></div>
				<div class="swiper-slide"><img src="../assets/clothes-bg.png"></div>
				<div class="swiper-slide"><img src="../assets/clothes-bg.png"></div>
	        </div>
			<!-- Add Pagination -->
			<div class="swiper-pagination"></div>
	    </div>
	    <div class="goods-text">
	    	<p class="goods-title">XXX</p>
	    	<p class="goods-price">99</p>
	    </div>
	</div>
</template>

<script>
import Swiper from '../../static/js/swiper.min.js'
export default {
	name: 'goods-detail',
	data () {
		return {
			msg:this.$route.params.gid
		}
	},
	filters: {

    },
	mounted:function(){
		console.log(this.$route.params.gid)
		var swiper = new Swiper('.swiper-container', {
			pagination: '.swiper-pagination',
			paginationClickable: true
	    });
	},
	methods:{

	},
}
</script>

<style scoped>
	.swiper-slide{
		text-align: center;
	}
	.swiper-slide img{
		width: 90%;
		margin: 2rem 0 3rem;
	}
	.goods-text{
		text-align: center;
	}
</style>





