<template>
	<div class="search">
		<div class="search-input">
			<div class="search-left">
				<div class="search-bg">
					<form onsubmit="return false;"><input type="search" placeholder="" id="searchKey"/></form>
				</div>
			</div>
			<div class="search-right">
				<div class="search-cancel" @click="back"></div>
			</div>
		</div>
		<div class="search-result">
			<ul class="search-result-list bottom-space">
				<li class="search-result-title"></li>
				<li class="search-result-value">
					<div class="search-result-img"><img src="../assets/ico_header.png"></div>
					<div class="search-result-text"></div>
				</li>
				<li class="search-result-value">
					<div class="search-result-img"><img src="../assets/ico_header.png"></div>
					<div class="search-result-text"></div>
				</li>
				<router-link tag="li" to="Designer" class="search-result-more"></router-link to="Designer">
			</ul>
			<ul class="search-result-list">
				<li class="search-result-title"></li>
				<li class="search-result-value">
					<div class="search-result-img"><img src="../assets/clothes-bg.png"></div>
					<div class="search-result-text"></div>
				</li>
				<li class="search-result-value">
					<div class="search-result-img"><img src="../assets/clothes-bg.png"></div>
					<div class="search-result-text"></div>
				</li>
				<li class="search-result-value">
					<div class="search-result-img"><img src="../assets/clothes-bg.png"></div>
					<div class="search-result-text"></div>
				</li>
				<li class="search-result-value">
					<div class="search-result-img"><img src="../assets/clothes-bg.png"></div>
					<div class="search-result-text"></div>
				</li>
				<li class="search-result-value">
					<div class="search-result-img"><img src="../assets/clothes-bg.png"></div>
					<div class="search-result-text"></div>
				</li>
				<router-link tag="li" to="Designer" class="search-result-more"></router-link to="Designer">
			</ul>
		</div>
	</div>
</template>

<script>
	export default {
	name: 'search',
	data () {
		return {
		}
	},
	mounted:function(){
		$("#searchKey").focus();
		this.$nextTick(function(){
			// this.matchsPage();
		})
	},
	methods:{
		back:function(){
			history.back();
		}
	},
}
</script>

<style scoped>
	.search-input{
		display: flex;
		display: -webkit-flex;
		align-items: center;
		-webkit-align-items: center;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		padding: 0.5rem 2rem 0.5rem 0.8rem;
		border-bottom: 1px solid #e5e5e5;
	}
	.search-left{
		width: 80%;
		background-color: #f5f5f5;
		border-radius: 0.312rem;
		padding: 0.3rem 0.5rem;
	}
	.search-bg{
		background: url("../assets/ico-search.png") left no-repeat;
		background-size: auto 1.0rem;
	}
	.search-left input{
		background:transparent;
		border:none;outline:none;
		-webkit-tap-highlight-color:transparent;
		padding-left: 1.5rem;
	}
	.search-cancel{
		font-size: 0.938rem;
	}
	.search-result-list{
		padding: 0.8rem 0.8rem 0;
	}
	.search-result-list li{
		border-bottom: 1px solid #e5e5e5;
	}
	.search-result-title{
		padding-bottom: 0.2rem;
	}
	.search-result-value{
		display: flex;
		align-items: center;
		padding: 0.5rem 0;
	}
	.search-result-img{
		width: 3rem;
		margin-right: 1rem;
	}
	.search-result-list .search-result-more{
		padding: 0.5rem 1.5rem;
		color: blue;
		background: url("../assets/ico-search.png") left no-repeat;
		background-size: auto 1.0rem;
		border-bottom: 0;
	}
</style>



